import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Container, TimeUnit } from "./style";

function Timer({ targetTime }) {
  const [remainTime, setRemainTime] = useState(
    new Date(targetTime - new Date())
  );

  useEffect(() => {
    const countdown = setInterval(() => {
      if (remainTime > 0) {
        remainTime.setSeconds(remainTime.getSeconds() - 1);
        setRemainTime(new Date(remainTime));
      } else {
        clearInterval(countdown);
      }
    }, 1000);
    return () => clearInterval(countdown);
  }, [remainTime]);

  return (
    <Container>
      <TimeUnit fontWeight="900">ë‚¨ì€ ì‹œê°„: &nbsp;</TimeUnit>
      <TimeUnit>{remainTime.getDate()} ì¼&nbsp;</TimeUnit>
      <TimeUnit>{remainTime.getHours()}ì‹œ&nbsp;</TimeUnit>
      <TimeUnit>{remainTime.getMinutes()}ë¶„&nbsp;</TimeUnit>
      <TimeUnit>{remainTime.getSeconds()}ì´ˆ</TimeUnit>
    </Container>
  );
}

Timer.propTypes = {
  targetTime: PropTypes.instanceOf(Date).isRequired,
};

export default Timer;
